* {
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans KR', sans-serif;
}
@font-face {
  font-family: 'Noto Sans KR';
  font-weight: 300;
  src: url('./font/Noto_Sans_KR/static/NotoSansKR-Regular.ttf') format('woff2'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Regular.ttf') format('woff'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'Noto Sans KR';
  font-weight: 500;
  src: url('./font/Noto_Sans_KR/static/NotoSansKR-Medium.ttf') format('woff2'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Medium.ttf') format('woff'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Medium.ttf') format('truetype');
}
@font-face {
  font-family: 'Noto Sans KR';
  font-weight: 700;
  src: url('./font/Noto_Sans_KR/static/NotoSansKR-Bold.ttf') format('woff2'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Bold.ttf') format('woff'),
    url('./font/Noto_Sans_KR/static/NotoSansKR-Bold.ttf') format('truetype');
}
li, ol {
  list-style: none;
}
a {
  text-decoration: none;
}
.App {
  padding: 0 20px;
}
.container {
  max-width: 1100px;
  padding: 30px 0;
  margin: 0 auto;
  margin-top: 60px;
  border: 1px solid #1c1c1c;
  border-radius: 10px;
  box-sizing: border-box;
}
.wrap {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 30px;
}
.todo_top > h2 {
  text-align:  center;
  font-weight: 700;
}
.todo_top form {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
}
.todo_top form input {
  width: calc(100% - 80px);
  border: none;
  border-bottom: 1px solid #1c1c1c;
  outline: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.todo_top form button {
  cursor: pointer;
  width: 70px; height: 40px;
  border: none;
  background-color: rgb(107, 122, 255);
  border-radius: 4px;
  color: #fff;
  font-size: 18px;
}

.todo_bottom {
  margin-top: 20px;
}
.todo_bottom li {
  display: flex;
  justify-content: space-between;
  background-color: rgb(176, 217, 255);
  border-radius: 4px;
  margin-bottom: 6px;
  padding: 5px 10px;
  font-weight: 500;
}
.todo_bottom ul li:last-of-type {
  margin-bottom: 0;
}
.todo_bottom ul li button {
  cursor: pointer;
  border: none;
  padding: 5px;
  border-radius: 4px;
  color: #fff;
}
.todo_bottom ul li .clear {
  background-color: rgb(60, 38, 253);
  margin-right: 4px;
}
.todo_bottom ul li .remove {
  background-color: rgb(253, 118, 118);
}
.todo_bottom li:has(.like) {
  background-color: #aaa;
  text-decoration: line-through 2px;
  color: #cecece;
}
.todo_bottom li:has(.like) .clear {
  background-color: #414141;
}